<template>
  <div>
    <b-card-group deck>
      <b-card
        header-tag="header"
        title="Create New Company"
      >
        <validation-observer ref="simpleRules">
          <b-form>
            <b-row>
              <!-- name -->
              <b-col md="6">
                <b-form-group
                  label="Name"
                  label-for="name"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="name"
                    rules="required"
                  >
                    <b-form-input
                      id="name"
                      v-model="name"
                      :state="errors.length > 0 ? false : null"
                      placeholder="Name"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <!-- email -->
              <b-col md="6">
                <b-form-group
                  label="Email"
                  label-for="email"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="email"
                    rules="required"
                  >
                    <b-form-input
                      id="email"
                      v-model="email"
                      :state="errors.length > 0 ? false : null"
                      placeholder="Email"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <!-- URL -->
              <b-col md="6">
                <b-form-group
                  label="URL"
                  label-for="url"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="url"
                    rules="required"
                  >
                    <b-form-input
                      id="url"
                      v-model="url"
                      :state="errors.length > 0 ? false : null"
                      placeholder="URL"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>

              <b-col md="6">
                <b-form-group
                  label="Logo"
                  label-for="Logo"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="Logo"
                    rules="required"
                  >
                    <b-form-file
                      id="logo"
                      v-model="image"
                      :state="errors.length > 0 ? false : null"
                      placeholder="Logo"
                      @change="fileCategory"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>

              <!-- create button -->
              <b-col cols="12">
                <b-button
                  class="mt-2"
                  variant="primary"
                  type="submit"
                  @click.prevent="validationForm"
                >
                  Add New Company
                </b-button>
              </b-col>
            </b-row>
          </b-form>
        </validation-observer>
      </b-card>
    </b-card-group>
  </div>
</template>

<script>
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { required } from '@/@core/utils/validations/validations'
import {
  BCard,
  BCardGroup,
  BButton,
  BFormInput,
  BFormGroup,
  BForm,
  BRow,
  BCol,
  // BFormSelect,
  BFormFile,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

const dictionary = {
  ar: {
    names: {
      name: 'اسم التصنيف',
      type: 'نوع التصنيف',
      image: 'صورة التصنيف',
    },
  },
}
localize('en', dictionary.en)
export default {
  name: 'AddCompany',
  components: {
    ValidationProvider,
    ValidationObserver,
    BCard,
    BCardGroup,
    BButton,
    BFormInput,
    BFormGroup,
    BForm,
    BRow,
    BCol,
    // BFormSelect,
    BFormFile,
  },
  data() {
    return {
      category_file: null,
      name: '',
      email: '',
      image: [],
      url: '',
      required,
    }
  },
  methods: {
    validationForm() {
      const formData = new FormData()
      formData.append('name', this.name)
      formData.append('email', this.email)
      formData.append('url', this.url)
      formData.append('logo', this.category_file)
      this.$refs.simpleRules.validate().then(success => {
        if (success) {
          this.$store.dispatch('company/store', formData).then(() => {
            this.$router.push('all-companies')
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'The company has been added successfully',
                icon: 'CheckCircleIcon',
                variant: 'success',
              },
            })
          })
        }
      })
    },
    fileCategory(event) {
      // eslint-disable-next-line prefer-destructuring
      this.category_file = event.target.files[0]
    },
  },
}
</script>

<style scoped></style>
